import React, { useState, useCallback, useEffect, Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import * as actions from "../../../store/actions";
import { Typography, Grid, Paper, Box, Button } from "@material-ui/core";
import TableSkeleton from "../../../components/common/TableSkeleton";
import AddRoundedIcon from "@material-ui/icons/AddRounded";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import ButtonSpinner from "../../../components/UI/Spinners/ButtonSpinner";
import { isError } from "../../../util/generals";
import Input from "../../../components/Inputs/Input";
import IosSwitch from "../../../components/Inputs/IosSwitch";
import CheckboxLabel from "../../../components/Inputs/CheckboxLabel";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";

const initialState = (lng) => {
  return {
    language: {
      value: lng ? lng.language : "",
      validity: lng ? !!lng.language : false,
    },
    code: {
      value: lng ? lng.code : "",
      validity: lng ? !!lng.code : false,
    },
    is_primary: {
      value: lng ? lng.is_primary + "" : "0",
      validity: true,
    },
    status: {
      value: lng ? lng.status : "1",
      validity: true,
    },
  };
};

let timeout;

const AddEdit = ({ history, match }) => {
  const isEdit = match.params.id;
  const [editLoading, setEditLoading] = useState(!!isEdit);
  const [state, setState] = useState(initialState());
  const [touched, setTouched] = useState(false);
  const languages = useSelector((state) => state.languages);
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const fetchLanguage = useCallback(async () => {
    try {
      const fetchedLanguage = await dispatch(actions.getLanguage(isEdit));
      setState(initialState(fetchedLanguage));
      setEditLoading(false);
    } catch (err) {
      let msg = err.message || t("settings:language_not_found");
      dispatch(actions.fireSnake("error", msg));
      history.replace("/settings/languages");
    }
  }, [isEdit, dispatch, t, history]);

  useEffect(() => {
    if (isEdit) {
      timeout = setTimeout(() => {
        fetchLanguage();
      }, 0);
    }
    return () => clearTimeout(timeout);
  }, [isEdit, fetchLanguage]);

  const handleState = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: { value: value, validity: validity },
      };
    });
  };

  const handleSubmit = async () => {
    let body = {};
    // validate non-nested state
    for (const key in state) {
      if (key !== "description") {
        body = { ...body, [key]: state[key].value };
        if (!state[key].validity) {
          setTouched(true);
          return dispatch(
            actions.fireSnake("error", t("common:complete_red_input"))
          );
        }
      }
    }

    try {
      await dispatch(actions.addEditLanguage(body, isEdit));
      history.replace("/settings/languages");
    } catch (err) {
      let msg = err.message || t("settings:save_lng_err");
      dispatch(actions.fireSnake("error", msg));
    }
  };

  return (
    <Fragment>
      <Typography align="center" variant="h4" className="py-1">
        {isEdit ? t("settings:edit_language") : t("settings:add_language")}
      </Typography>
      {editLoading ? (
        <Paper>
          <TableSkeleton row={4} col={2} />
        </Paper>
      ) : (
        <Fragment>
          <Paper className="my-1 p">
            <Box className="flex flex-justify-between align-items-center py">
              <Typography align="left" variant="h6" className="p">
                {t("settings:language_detail")}
              </Typography>
              <Box>
                <CheckboxLabel
                  state={+state.is_primary.value > 0}
                  name="is_primary"
                  label={t("settings:is_primary")}
                  onChange={(value, validity, name) =>
                    handleState(value ? "1" : "0", validity, name)
                  }
                />
                <IosSwitch
                  state={state.status.value}
                  onChange={handleState}
                  name="status"
                />
              </Box>
            </Box>
            <Grid container spacing={1}>
              {/* Language Description */}
              <Grid item sm={6} xs={12}>
                <Input
                  label={t("settings:language_name") + " **"}
                  initialValue={state.language.value}
                  name="language"
                  getResult={handleState}
                  required
                  min={2}
                  setTouched={touched}
                  serverError={isError(
                    languages.errors && languages.errors.data,
                    `language`
                  )}
                />
              </Grid>
              <Grid item sm={6} xs={12}>
                <Input
                  label={t("settings:code") + " **"}
                  initialValue={state.code.value}
                  name="code"
                  getResult={handleState}
                  required
                  min={1}
                  max={3}
                  setTouched={touched}
                  serverError={isError(
                    languages.errors && languages.errors.data,
                    `code`
                  )}
                  helperNotice={t("settings:lang_code_notice")}
                  withIcon={
                    <a
                      href="https://en.wikipedia.org/wiki/List_of_ISO_639-1_codes"
                      target="_blank"
                      rel="noopener noreferrer"
                      style={{ fontSize: 10 }}
                      className="flex"
                    >
                      <LinkRoundedIcon fontSize="small" />{" "}
                      {t("settings:codes_list")}
                    </a>
                  }
                />
              </Grid>
            </Grid>
          </Paper>
          {/* Submit */}
          <Box className="center py-1">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleSubmit}
              endIcon={languages.itemLoading && <ButtonSpinner />}
              startIcon={isEdit ? <EditRoundedIcon /> : <AddRoundedIcon />}
              disabled={languages.itemLoading}
              style={{ width: "80%" }}
            >
              <Typography variant="h4" className="py-1">
                {isEdit
                  ? t("settings:edit_language")
                  : t("settings:add_language")}
              </Typography>
            </Button>
          </Box>
        </Fragment>
      )}
    </Fragment>
  );
};

export default AddEdit;
