import React from "react";
import { FormControlLabel, Checkbox, Typography } from "@material-ui/core";

const CheckboxLabel = ({
  state,
  name,
  label,
  onChange,
  style,
  textStyle,
  disabled,
  required,
}) => {
  return (
    <FormControlLabel
      control={
        <Checkbox
          checked={state}
          onChange={(e) =>
            onChange(
              e.target.checked,
              required ? !!e.target.checked : true,
              name
            )
          }
          name={name}
          color="primary"
          size="small"
          style={style}
          disabled={disabled}
        />
      }
      label={
        <Typography component="span" variant="body2" style={textStyle}>
          {label}
        </Typography>
      }
    />
  );
};

export default CheckboxLabel;
